import React from 'react'

const searchContainer = {
  display: 'flex',
    padding: '6px 9px 6px 9px',
    background: '#fff',
    backgroundImage: 'url("data:image/png;base64,")',
    backgroundRepeat: 'no-repeat',
    backgroundSize: '27px 20px',
    backgroundPosition: '9px center'
};

const searchBar = {
    display: 'block',
    background: '#ccc',
    borderRadius: '4px',
    height: '25px',
    lineHeight: '25px',
    textAlign: 'center',
    color: '#666',
    flexGrow: 1
};

const Search = (props) => {
    return (
        <div>
            <div style={searchContainer}>
                <a style={searchBar}>寻找宝贝店铺</a>
                <div onClick={() => props.history.push('/')} style={{padding: '0 8px', lineHeight: '25px'}}>取消</div>
            </div>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
        </div>
    )
}

export default Search
