
let defaultStore = {
    data: [],
    shoucange: {},
    userinfo: null
}

function reducers (state = defaultStore, action) {
    const newStore = JSON.parse(JSON.stringify(state));
    switch (action.type) {
        case 'INIT':
            newStore.data = action.payload;
            return newStore;
        case 'LOGIN':
            newStore.userinfo = action.payload;
            return newStore;
        case 'ADD_SHOUCANG':
            newStore.shoucange[action.payload] = true;
            return newStore;
        case 'REMOVE_SHOUCANG':
            newStore.shoucange[action.payload] = false;
            return newStore;
        default:
            break;
    }
    return state;
}
export default reducers;