import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

const Login = ({ login, history }) => {
  return (
    <div style={{ display: "flex", alignItems: 'center', justifyContent: 'center', paddingTop: '40px' }}>
      <button onClick={async () => {
        await login()
        history.push('/');
      }}>点击登录</button>
    </div>
  );
}

export default withRouter(connect(() => {
  return {}
}, dispatch => {
  return {
    async login () {
      const data = await fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login').then(res => res.json()).then(res => res.data);
      dispatch({
        type: "LOGIN",
        payload: data
      })
    }
  }
})(Login))
