import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

const Item = (props) => {
  const id = props.match.params.id;
  const data = props.data.find(item => {
    return item.id == id;
  });
  if (!data) return null;
  const isSc = props.shoucange[id];
  return (
    <div style={{ display: 'flex', height: '100vh', flexDirection: 'column', justifyContent: 'space-between' }}>
      <div style={{ height: '300px', backgroundSize: 'cover', backgroundPosition: '50% 50%', padding: '16px', backgroundImage: 'url(' + data.img + ')' }}>
        <a onClick={() => props.history.push('/')} style={{ display: 'block', height: '24px', width: "24px", backgroundColor: 'rgba(0,0,0,.5)', borderRadius: '50%', color: "#fff", boxSizing: 'border-box', padding: '4px 6px' }}>{"<"}</a>
      </div>
      <div style={{ flex: 1 }}>
        <div style={{ backgroundColor: "#fff", color: "#f00", padding: '10px 24px 4px', fontSize: '20px', fontWeight: 'bold' }}><span style={{ fontSize: '16px', paddingRight: '2px' }}>¥</span>{data.price}</div>
        <div style={{ padding: '12px', fontSize: '18px', fontWeight: 'bold', backgroundColor: "#fff", color: '#333' }}>{data.title}</div>
      </div>

      <div style={{ display: 'flex', justifyContent: 'space-around', fontSize: '12px', alignItems: 'center', padding: '10px 16px', backgroundColor: '#fff' }}>
        <a>店铺</a>
        <a>客服</a>
        {/* <a></a> */}
        <div onClick={() => {
          if (props.userinfo) {
            props.changeSc(id, !isSc)
          } else {
            props.history.push('/login')
          }
        }} style={{ display: 'flex', flexDirection: "column", alignItems: 'center', justifyContent: 'center' }} >
          <i className={"iconfont " + (isSc ? 'icon-shoucang1' : 'icon-shoucang')} style={{ fontSize: '24px' }}></i>
          <span >收藏</span>
        </div>
        <div>
          <a style={{ borderRadius: '1rem 0 0 1rem', backgroundImage: 'linear-gradient(to right, #FFC500, #FF9402)', padding: '6px 16px', color: '#fff', fontWeight: 'bold' }}>加入购物车</a>
          <a style={{ borderRadius: '0 1rem 1rem 0', backgroundImage: 'linear-gradient(to right, #FF7A00, #FE560A)', padding: '6px 16px', color: '#fff', fontWeight: 'bold' }}>立即购买</a>
        </div>
      </div>
    </div >
  )
}

export default withRouter(connect(state => {
  return {
    data: state.data,
    shoucange: state.shoucange,
    userinfo: state.userinfo
  }
}, (dispatch) => {
  return {

    changeSc (id, state) {
      const type = state ? 'ADD_SHOUCANG' : 'REMOVE_SHOUCANG';
      dispatch({
        type,
        payload: id
      })
    }
  }
})(Item))
