import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom';

const Cart = (props) => {
    useEffect(() => {
        if (!props.userinfo) {
            props.history.push('/login')
        }
    }, [props]);
    return (
        <div>
            <div style={{ marginBottom: "10px", padding: '10px', fontSize: '24px', fontWeight: 'bold', color: '#333' }}>我的收藏</div>
            {
                props.shoucangeList.map(item => {
                    return <div key={item.id} style={{ padding: '10px', marginBottom: '6px', backgroundColor: '#fff', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                        <span>{item.title}</span>
                        <button onClick={() => { props.changeSc(item.id) }}>取消收藏</button>
                    </div>
                })
            }
            {
                !props.shoucangeList || !props.shoucangeList.length ? (
                    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '20px', marginTop: '50px' }}>没有收藏</div>
                ) : null
            }
        </div>
    )
}

export default withRouter(connect(state => {
    const shoucangeList = Object.keys(state.shoucange).filter(key => state.shoucange[key]).map(key => {
        return state.data.find(item => {
            return item.id == key;
        });
    });
    return {
        shoucangeList,
        userinfo: state.userinfo
    }
}, (dispatch) => {
    return {
        changeSc (id) {
            dispatch({
                type: 'REMOVE_SHOUCANG',
                payload: id
            })
        }
    }
})(Cart));
