import { Carousel } from 'antd-mobile'
import Item from 'antd-mobile/lib/popover/Item';
import React from 'react'
import { connect } from 'react-redux';
// import { Items } from './Item';

const contentStyle = {
    height: '140px',
    color: '#fff',
    lineHeight: '140px',
    textAlign: 'center',
    background: '#364d79',
    backgroundSize: '100% 100%',
    backgroundPosition: '50% 50%',
};

const searchContainer = {
    padding: '6px 9px 6px 42px',
    background: 'rgb(255, 133, 42)',
    backgroundImage: 'url("data:image/png;base64,")',
    backgroundRepeat: 'no-repeat',
    backgroundSize: '27px 20px',
    backgroundPosition: '9px center'
};

const searchBar = {
    display: 'block',
    background: 'rgb(255, 78, 34)',
    borderRadius: '4px',
    height: '25px',
    lineHeight: '25px',
    textAlign: 'center',
    color: 'rgb(240, 224, 220)'
};

const itemCover = {
    height: 180,
    backgroundSize: '100% 100%',
    backgroundPosition: '50% 50%',
    margin: '-8px -8px 8px -8px'
}

const Home = (props) => {
    return (
        <div>
            <div style={searchContainer}>
                <a onClick={() => props.history.push('/search')} style={searchBar}>寻找宝贝店铺</a>
            </div>
            <Carousel infinite autoplay>
                <div>
                    <div style={{ ...contentStyle, backgroundImage: 'url("https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg")' }}></div>
                </div>
                <div>
                    <div style={{ ...contentStyle, backgroundImage: 'url("https://gw.alicdn.com/imgextra/i4/81/O1CN01fDWkiu1CT8iAkGF6M_!!81-0-lubanu.jpg")' }}></div>
                </div>
                <div>
                    <div style={{ ...contentStyle, backgroundImage: 'url("https://gw.alicdn.com/imgextra/i4/94/O1CN016EMBmd1CZ5rQwKF7U_!!94-0-lubanu.jpg")' }}></div>
                </div>
            </Carousel>
            {/* <img src={img} alt=""/>
            <img src="https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg" alt=""/> */}
            <div style={{ display: 'flex', flexWrap: 'wrap', marginBottom: "100px" }}>
                {
                    props.data.map((item, i) => <div style={{ flexBasis: '50%' }} key={item.id}>
                        <div onClick={() => props.history.push('/detail/' + item.id)} style={{ margin: '8px', backgroundColor: '#fff', padding: '8px', height: 250, boxShadow: '0 1px 1px rgba(97,127,152,.2),1px 0 0 rgba(97,127,152,.1),-1px 0 0 rgba(97,127,152,.1)' }}>
                            <div style={{ ...itemCover, backgroundImage: 'url(' + '"' + item.img + '"' + ')' }}></div>
                            <p style={{ overflow: 'hidden', height: '32px' }}>{item.title}</p>
                            <p><span style={{ color: '#ff5500', fontSize: '18px' }}>￥{item.price}</span> {(item.price * Math.random() * 10) | 0}人已购买</p>
                        </div>
                    </div>)
                }
            </div>

        </div>
    )
}

export default connect((state) => {
    return {
        data: state.data
    }
})(Home)
